// src/lib/users.ts
const API_BASE = "http://localhost:3001/api";

/* =======================
   TYPES
======================= */
export type User = {
  id: number;
  email: string;
  role: "admin" | "staff" | "user";

  first_name?: string;
  last_name?: string;
  phone?: string;
  bio?: string;

  country?: string;
  city?: string;
  state?: string;
  postal_code?: string;
  tax_id?: string;

  created_at?: string;
};

/* =======================
   HELPERS
======================= */
function authHeaders() {
  const token = localStorage.getItem("token");

  if (!token) {
    throw new Error("No auth token");
  }

  return {
    "Content-Type": "application/json",
    Authorization: `Bearer ${token}`,
  };
}

/* =======================
   API CALLS
======================= */

// ‚úÖ GET USERS
export async function getUsers(): Promise<User[]> {
  try {
    const res = await fetch(`${API_BASE}/users`, {
      headers: authHeaders(),
    });

    const data = await res.json();

    console.log("üî• /api/users response:", data);

    if (!res.ok) {
      console.error("GET USERS FAILED:", data);
      return [];
    }

    // ‚úÖ backend tr·∫£ ARRAY TR·ª∞C TI·∫æP
    if (Array.isArray(data)) {
      return data;
    }

    // (fallback n·∫øu sau n√†y ƒë·ªïi backend)
    if (Array.isArray(data.users)) {
      return data.users;
    }

    console.warn("‚ö†Ô∏è Unexpected users response shape:", data);
    return [];
  } catch (err) {
    console.error("GET USERS ERROR:", err);
    return [];
  }
}

// ‚úÖ CREATE USER
export async function createUser(data: {
  email: string;
  password: string;
  role: "admin" | "staff" | "user";
}): Promise<User> {
  const res = await fetch(`${API_BASE}/users`, {
    method: "POST",
    headers: authHeaders(),
    body: JSON.stringify({
      email: data.email,
      password: data.password,
      password_confirmation: data.password,
      role: data.role,
    }),
  });

  const result = await res.json();

  if (!res.ok) {
    throw new Error(result.errors?.join(", ") || "Create user failed");
  }

  // backend render user_json
  return result;
}

// ‚úÖ UPDATE USER
export async function updateUser(
  id: number,
  payload: Partial<User> & {
    password?: string;
    password_confirmation?: string;
  }
): Promise<User> {
  const res = await fetch(`${API_BASE}/users/${id}`, {
    method: "PATCH",
    headers: authHeaders(),
    body: JSON.stringify(payload),
  });

  const data = await res.json();

  if (!res.ok) {
    throw new Error(data.errors?.join(", ") || "Update failed");
  }

  return data;
}

// ‚úÖ DELETE USER
export async function deleteUser(id: number): Promise<void> {
  const res = await fetch(`${API_BASE}/users/${id}`, {
    method: "DELETE",
    headers: authHeaders(),
  });

  if (!res.ok) {
    throw new Error("Failed to delete user");
  }
}
